"use client";

import React, { useRef, useState, useEffect, Suspense } from 'react';
import * as THREE from 'three';
import { Canvas, useFrame } from '@react-three/fiber';
import { View, OrbitControls, useGLTF, Center } from '@react-three/drei';

function Model({ url }: { url: string }) {
  const { scene } = useGLTF(url);
  
  // This hook runs every frame - useful for checking if model exists
  useEffect(() => {
    if (scene) {
      // Auto-scale logic: fit model into a 1x1x1 box roughly
      const box = new THREE.Box3().setFromObject(scene);
      const size = box.getSize(new THREE.Vector3()).length();
      const scale = 2 / size; 
      scene.scale.set(scale, scale, scale);
    }
  }, [scene]);

  return <primitive object={scene} />;
}

const Scene = ({ modelUrl, panelRef }: { modelUrl: string, panelRef: React.RefObject<HTMLDivElement> }) => {
  const [domReady, setDomReady] = useState<HTMLElement | null>(null);

  useEffect(() => {
    if (panelRef.current) setDomReady(panelRef.current);
  }, [panelRef]);

  return (
    <>
      <ambientLight intensity={2} />
      <pointLight position={[10, 10, 10]} intensity={2} />
      
      <Suspense fallback={null}>
        <Center>
          <Model url={modelUrl} />
        </Center>
      </Suspense>

      {domReady && (
        <OrbitControls 
          makeDefault 
          enableZoom={false} 
          enablePan={false} 
          domElement={domReady} 
        />
      )}
    </>
  );
};

export default function MultiScenePage() {
  const [mounted, setMounted] = useState(false);
  const containerRef = useRef<HTMLDivElement>(null!);
  const view1Ref = useRef<HTMLDivElement>(null!);
  const view2Ref = useRef<HTMLDivElement>(null!);

  useEffect(() => {
    setMounted(true);
  }, []);

  if (!mounted) return null;

  return (
    <div ref={containerRef} className="relative w-full h-screen bg-gray-100 p-8 flex gap-4">
      
      {/* HTML DOM Elements */}
      <div 
        ref={view1Ref} 
        className="relative flex-1 bg-white border-2 border-dashed border-blue-400 rounded-2xl z-10" 
      />
      
      <div 
        ref={view2Ref} 
        className="relative flex-1 bg-white border-2 border-dashed border-red-400 rounded-2xl z-10" 
      />

      {/* THREE.JS CANVAS */}
      <Canvas 
        // pointer-events-none is CRITICAL. It lets mouse clicks pass THROUGH the canvas 
        // to the divs below, which the OrbitControls then catch.
        className="pointer-events-none fixed inset-0 z-50"
      >
        <View track={view1Ref}>
          <Scene 
            panelRef={view1Ref}
            modelUrl="https://raw.githubusercontent.com/KhronosGroup/glTF-Sample-Models/master/2.0/Duck/glTF/Duck.gltf" 
          />
        </View>

        <View track={view2Ref}>
          <Scene 
            panelRef={view2Ref}
            modelUrl="https://raw.githubusercontent.com/KhronosGroup/glTF-Sample-Models/master/2.0/Box/glTF/Box.gltf" 
          />
        </View>
      </Canvas>
    </div>
  );
}